import { Component, OnInit } from '@angular/core';
import { Poll as AutoevaluacionPoll } from 'src/app/entities/autoevaluacion-covid19';
import { Poll as ChequeoPoll } from 'src/app/entities/chequeo-covid19';
import { UtilsService } from 'src/app/services/utils.service';
import { AutoevaluacionCOVID19Service } from 'src/app/services/autoevaluacion-covid19.service';
import { Geolocation as GeolocationService } from '@ionic-native/geolocation/ngx';
import { NativeGeocoder } from '@ionic-native/native-geocoder/ngx';
import { AlertController } from '@ionic/angular';
import { ChequeosCOVID19Service } from 'src/app/services/chequeo-covid19.service';
import { Router } from '@angular/router';
import { finalize } from 'rxjs/operators';
import { Geolocation } from 'src/app/entities/condiciones-salud';
import { alertCommunicationError } from 'src/app/commons/user-feedback';

/**
 * Componente de la vista de rechazo a las políticas de tratamiento de datos personales.
 */
@Component({
  selector: 'app-condiciones-salud-rechazo',
  templateUrl: './condiciones-salud-rechazo.page.html',
  styleUrls: ['./condiciones-salud-rechazo.page.scss'],
})
export class CondicionesSaludRechazoPage implements OnInit {

  /**
   * Encuesta.
   */
  poll: AutoevaluacionPoll | ChequeoPoll;

  /**
   * Indica si debe mostrarse un mensaje de error de conexión con el servicio.
   */
  showError: boolean;

  constructor(
    private utils: UtilsService,
    private autoevaluacionService: AutoevaluacionCOVID19Service,
    private geolocation: GeolocationService,
    private nativeGeocoder: NativeGeocoder,
    private alertCtrl: AlertController,
    private chequeoService: ChequeosCOVID19Service,
    private router: Router
  ) { }

  ngOnInit(): void {
    this.poll = this.router.getCurrentNavigation().extras.state.poll;
    this.getIPAddress();
  }

  /**
   * Redirige a la vista de inicio de la aplicación.
   */
  goToHome(): void {
    this.router.navigate(['/home']);
  }

  /**
   * Obtiene la dirección _IP_ pública desde la cual se realizan las peticiones.
   */
  private async getIPAddress(): Promise<void> {
    await this.utils.presentLoading();

    this.utils.getIPAddress()
      .pipe(finalize(() => this.utils.dismissLoading()))
      .subscribe(
        r => {
          const body = r;
          const ip = body.query;
          this.poll.userData.ip = ip;
          this.getCurrentPosition();
        },
        () => this.getCurrentPosition()
      );
  }

  /**
   * Obtiene las coordenadas de ubicación física del dispositivo.
   *
   * @param poll Encuesta.
   */
  private async getCurrentPosition(): Promise<void> {
    const onFinally = (latitude?: string, longitude?: string): void => {
      this.utils.dismissLoading();
      const geolocation = new Geolocation();
      geolocation.latitude = latitude;
      geolocation.longitude = longitude;
      this.poll.geolocation = geolocation;
      this.getAddress();
    };

    await this.utils.presentLoading();

    this.geolocation.getCurrentPosition()
      .then(r => onFinally(`${r.coords.latitude}`, `${r.coords.longitude}`))
      .catch(() => onFinally());
  }

  /**
   * Obtiene la dirección descriptiva a partir de las coordenadas obtenidas del dispositivo.
   *
   * @param poll Encuesta.
   */
  private async getAddress(): Promise<void> {
    const onFinally = (address?: string): void => {
      this.utils.dismissLoading();
      this.poll.geolocation.address = address;
      this.sendPoll();
    };

    const latitude = this.poll.geolocation.latitude;
    const longitude = this.poll.geolocation.longitude;

    if (!latitude || !longitude) {
      return this.sendPoll();
    }

    await this.utils.presentLoading();

    this.nativeGeocoder.reverseGeocode(+latitude, +longitude)
      .then(r => {
        // Calle o carrera
        let address = !!r[0].thoroughfare ? r[0].thoroughfare : '';

        // Número
        address = !!r[0].subThoroughfare ?
          address !== '' ? `${address} #${r[0].subThoroughfare}` : `#${r[0].subThoroughfare}`
          : address;

        // Ciudad
        address = !!r[0].administrativeArea ?
          address !== '' ? `${address}, ${r[0].subAdministrativeArea}` : `${r[0].subAdministrativeArea}`
          : address;

        // Departamento
        address = !!r[0].administrativeArea ?
          address !== '' ? `${address}, ${r[0].administrativeArea}` : `${r[0].administrativeArea}`
          : address;

        onFinally(address);
      })
      .catch(() => onFinally());
  }

  /**
   * Envía la encuesta para su registro.
   *
   * @param poll Encuesta.
   */
  private async sendPoll(): Promise<void> {
    const body = this.poll.deserialize();
    let submitFn: any;
    let provider: any;

    if (this.poll instanceof ChequeoPoll) {
      submitFn = this.chequeoService.submitPoll;
      provider = this.chequeoService;
    } else {
      submitFn = this.autoevaluacionService.submitPoll;
      provider = this.autoevaluacionService;
    }

    await this.utils.presentLoading();

    submitFn.call(provider, body)
      .pipe(finalize(() => this.utils.dismissLoading()))
      .subscribe(
        () => { },
        () => {
          alertCommunicationError(this.alertCtrl);
          this.router.navigate(['/home']);
        }
      );
  }

}
